body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-animation: App-logo-spin infinite 6s linear;
          animation: App-logo-spin infinite 6s linear;
  height: 20vmin;
  box-shadow: inset 1px 1px 250px 50px #396;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #0ea4d6;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotateZ(0deg) rotateY(0deg) rotateX(0deg);
            transform: rotateZ(0deg) rotateY(0deg) rotateX(0deg);
  }
  to {
    -webkit-transform: rotateZ(360deg) rotateY(360deg) rotateX(360deg);
            transform: rotateZ(360deg) rotateY(360deg) rotateX(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotateZ(0deg) rotateY(0deg) rotateX(0deg);
            transform: rotateZ(0deg) rotateY(0deg) rotateX(0deg);
  }
  to {
    -webkit-transform: rotateZ(360deg) rotateY(360deg) rotateX(360deg);
            transform: rotateZ(360deg) rotateY(360deg) rotateX(360deg);
  }
}

